/* General */
body {
  background-color: white;
  /* margin-left:100px;
  margin-right:100px; */
}
.content{
  padding-top:150px;
}

h1{
  font-family:'Colfax Bold';
  font-size:100px;
  color:#0E2552;
}
h2{
  font-family:'Colfax Bold';
  color:white;
  font-size:54px;

}
h3{
  font-family:'Colfax Medium';
  font-size:30px;
  color:#004730;
}

@media only screen and (max-width: 600px) {
  h1{
    font-size:36px;
  }
}

.square{
  transform: rotate(-30deg);
  background-image: linear-gradient(194deg, #713C3C 25%, #2B2E33 100%);
  border-radius: 75px;
  height:500px;
  width:500px;
  position:absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top:50px;
  z-index: -1;
  /* box-shadow: 0 19px 38px rgba(0,0,0,0.1), 0 8px 15px rgba(0,0,0,0.1); */
  /* box-shadow: 0 19px 38px 0 rgba(0,0,0,0.15), inset 0 4px 13px 0 #282828; */


}
.circle{
  transform: rotate(-30deg);
  background: #3B3F47;
  border-radius: 500px;
  height:400px;
  width:400px;
  position:absolute;
  top:50px;
  right:100px;
  z-index: -1;
  box-shadow: 0 19px 38px rgba(0,0,0,0.1), 0 8px 15px rgba(0,0,0,0.1);
  /* box-shadow: 0 19px 38px 0 rgba(0,0,0,0.15), inset 0 4px 13px 0 #282828; */
}


@media only screen and (max-width: 990px) {
  .circle{
    /* background:none; */
  }
  .square{
    background:none;
    box-shadow:none;
  }
}

.navigation{
  display:inline-block;
  padding-top: 36px;
}
@media only screen and (max-width: 990px) {
  .navigation{
    padding-left:36px;

  }
}

.item{
  margin-right:24px;
  font-size:24px;
  font-weight: 700;
  float:left;
}

/* Bootstrap */
.container{
  padding-right:0px;
  padding-left:0px;
  margin-left:0px!important;
  margin-right:0px!important;
  margin:auto!important;



}
@media only screen and (max-width: 990px) {
  .container{
    padding-left:36px;
    padding-right:36px;

  }
}

/* Thumbnail.js */
.project{
  height:300px;
  width:100%;  
  border-radius:15px;
  margin-top:48px;
  transition: all 0.3s;
  background-image: linear-gradient(180deg, #4A81FD 0%, #244AFB 100%);

}
.card-blue{
  background-image: linear-gradient(180deg, #4A81FD 0%, #244AFB 100%);

}
.card-yellow{
  background: #FDC830;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #F37335, #FDC830);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #F37335, #FDC830); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ 
}
.card-green{
  background: #649173;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #DBD5A4, #649173);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #DBD5A4, #649173); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.project:hover{
  cursor:pointer;
  position: relative; 
  transform: translateY(-10px);
  transition: all 0.3s;
}
a{
  color:#979797;
  transition: all 0.1s;
  text-decoration: none;

}
a:hover{
  color:white;
  transition: all 0.1s;
  text-decoration: none;  
}
.project-title{
  font-family:'Colfax Bold';
  font-size: 24px;
  padding-left:36px;
  width: 100%; 
}
.project-category{
  font-family:'Colfax Light';
  font-size: 24px;
  padding-left:36px;
}
.project-img{
  height:66%;

}

/* Homepage */
.profile{
  margin-top:-54px;
}
.profile-pic{
  border-radius: 1000px;
  border: 8px solid white;
  height:200px;
}
@media only screen and (max-width: 990px) {
  .profile-pic{
    display: none;
  }
}

.header{
  background-image: url("/background.png");
  background-size: cover;
  background-repeat: no-repeat;
  height:600px;
  background-position-y: 200px; 
  
}
.work-header{
  border-top-left-radius: 1000px 100px;
  border-top-right-radius: 1000px 100px;
  background-color:#160A0A;
  margin-top:-100px;
  padding-top:200px;
  padding-bottom:200px;
}
.footer{
  height:500px;
  background-color: #191E24;
  padding-top:100px;
}
.highlight{
  background: #BEFFF6;
  border-radius: 74px;
  padding-top:36px;
  padding-bottom:24px;
  padding-left:54px;

}